___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --background: #101114;
    --primary-color: #1c1d20;
    --secondary-color: #4a4d57;
    --accent-color: #00ffc4;
    --text-color: #f9f9f9;
}

*,
*::before,
*::after {
    margin: 0;
    padding: 0;
}
html {
    font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
    font-size: 1rem;
    color: var(--text-color);
}

body {
    min-height: 100vh;
    padding: 0.6em;
    background-color: var(--background);
    display: flex;
    flex-direction: column;
    align-items: center;
}

h1 {
    margin-top: 100px;
    margin-bottom: 20px;
    font-size: 3rem;
    font-weight: 800;
    text-transform: uppercase;
    color: var(--accent-color);
}

.wrapper {
    width: 45rem;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    gap: 0.6rem;
}

#todo-input {
    box-sizing: border-box;
    padding: 0.7rem 1.2rem;
    width: 100%;
    background: none;
    border: 0.1rem solid var(--secondary-color);
    border-radius: 10rem;
    font: inherit;
    color: var(--text-color);
    caret-color: var(--accent-color);
}

#todo-input:focus {
    outline: none;
}

form {
    position: relative;
}

#add-button {
    position: absolute;
    top: 0;
    right: 0;

    background-color: var(--accent-color);
    height: 100%;
    padding: 0 2rem;
    border: none;
    border-radius: 10rem;
    font: inherit;
    font-weight: 600;
    color: var(--background);
    cursor: pointer;
}

.todo {
    margin-bottom: 0.6rem;
    padding: 0 1em;
    background-color: var(--primary-color);
    border-radius: 1rem;
    display: flex;
    align-items: center;
}

.todo-text {
    padding: 1rem;
    padding-right: 0;
    flex-grow: 1;
}

.delete-button {
    padding: 0.2rem;
    background: none;
    border: none;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.delete-button svg {
    transition: 0.2s ease;
}

.delete-button:hover svg {
    fill: #ff0033;
}

.custom-checkbox {
    border: 0.2rem solid var(--accent-color);
    border-radius: 50%;
    min-height: 1.2rem;
    min-width: 1.2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    transition: 0.2s ease;
    cursor: pointer;
}

input[type="checkbox"]:checked ~ .custom-checkbox {
    background-color: var(--accent-color);
}

input[type="checkbox"]:checked ~ .custom-checkbox svg {
    fill: var(--primary-color);
}

input[type="checkbox"]:checked ~ .todo-text {
    text-decoration: line-through;
    color: var(--secondary-color);
}

input[type="checkbox"] {
    display: none;
}

@media (max-width: 30em) {
    html {
        font-size: 0.7rem;
    }

    #add-button {
        position: unset;
        width: 100%;
        margin-top: 0.6rem;
        padding: 1em;
        height: auto;
    }

    h1 {
        margin-top: 4rem;
        font-size: 15vw;
    }
}
